html, body {
    // overflow-y: hidden;
    overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    &:hover, &:focus {
        text-decoration: none;
        color: inherit;
    }
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 230px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #49abdc;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}






.owl-carousel .owl-item img {
    width: auto;
}




.search-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background-color: #000000cc;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #49abdc;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .zx-search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
    border-radius: 50px;
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      padding: 5px 25px;
      box-shadow: 0px 0px 10px 0px #00000080;
      &::placeholder {
        font-family: ar-reg;
      }
    }
    button {
      width: 50px;
      height: 100%;
      position: absolute;
      top: 50%;
      left: 3px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
      // background-color: #49abdc;
      color: #000;
      font-size: 18px;
      [dir="ltr"] & {
        left: unset;
        right: 3px;
      }
      &:hover {
        color: #000;
      }
    }
  }
}









/*------ upperbar start ------*/
.upper-bar {
  background-color: #fff;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  .upper-left, .upper-right {
    width: 360px;
    display: flex;
  }
  .upper-left {
    
  }
  .upper-right {
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    top: 15px;
    left: 0;
    [dir="ltr"] & {
      left: unset;
      right: 0;
    }
  }
  .lang-switcher, .cur-switcher {
    position: relative;
    font-family: ar-reg;
    font-size: 12px;
    color: #999;
    margin: 0 15px 0 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    [dir="ltr"] & {
      margin: 0 0 0 15px;
    }
    > span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: "";
        font-family: "Linearicons-Free";
        font-weight: 600;
        font-size: 8px;
        color: #999;
        padding: 2px 4px 0 4px;
        transition: all .3s ease-in-out;
      }
    }
    img {
      margin: 0 5px;
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 55%;
      z-index: 9;
      transform: translateX(-50%);
      min-width: 110px;
      width: max-content;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
      color: #000;
      border-radius: 5px;
      padding: 8px 5px;
      transition: all .5s ease-in-out;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      a {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 3px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #49abdc;
        }
      }
      img {
        width: 20px;
        height: 14px;
        margin: 0 5px;
      }
      span {
        padding: 0 2px;
      }
    }
    &:hover {
      > span {
        color: #49abdc;
        &::after {
          color: #49abdc;
        }
      }
      .switch {
        top: 23px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
/*------ upperbar end ------*/




