  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // background: linear-gradient(0deg, #000, #fff);
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: flex-end;
    transition: all .5s ease-in-out;
    .inner {
      text-align: center;
      p {
        font-family: ar-md;
        font-size: 36px;
        color: #fff;
        margin: 0 0 60px 0;
        position: relative;
        &::after {
          content: "";
          width: 30px;
          height: 2px;
          background-color: rgb(255, 255, 255);
          transition: all .3s ease-in-out;
          position: absolute;
          bottom: -15px;
          left: 50%;
          transform: translateX(-50%);
        }
        &:hover {
          &::after {
            width: 100%;
          }
        }
      }
