/*--- card start ---*/
.card {
  // width: 330px;
  // min-height: 220px;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  &:hover {
    img {
      transform: scale(1.05);
    }
  }
  .card-img {
    height: 266px;
    height: 266px;
    border-style: solid;
    border-width: 2px;
    border-color: rgb(237, 237, 237);
    // background-color: #f7f7f7;
    margin: 0 0 0px 0;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      transition: all .5s ease-in-out;
      mix-blend-mode: multiply;
    }
  }
  .card-body {
    padding: 0 10px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
  }
  .title {
    font-family: ar-reg;
    font-size: 16px;
    color: #333;
    margin: 15px 0 10px 0;
    padding: 0 0 5px 0;
    // height: 47px;
    overflow: hidden;
  }
  .price-add {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .price {
    font-family: ar-bold;
    font-size: 18px;
    color: #3399cc;
    // margin: 0 0 15px 0;
    // padding: 10px 0 5px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    .old {
      font-family: ar-md;
      font-size: 16px;
      color: #999;
